import React from 'react';
//mui
import { Grid } from '@mui/material';

// helpers
import { join} from '../../../join' ;

export const HeadAlarmas = () => {

     const Square = ( {arg,idx=1} ) => {
          return(
               <div style={{border:'#c4c4c4 2px solid', borderRadius: idx===0  ? '13px 0px 0px 0px':idx===8 ? '0px 13px 0px 0px': '0px 0px 0px 0px'}}
                    className={join(['textField','text_color_title','text_bold','text_center','font_monserrat','fullwidth'])}
               >
                    {arg}
               </div>
          );
     };

     return(
          <Grid
               container
               direction='row'
               style={{border:' #f0f0f0 2px solid', borderRadius: '15px 15px 0px 0px' }}
          >
               <Grid item xs={4}>
                    <span></span>
               </Grid>
               {
                    ['1','2','3','4','5','6','7','8'].map( (elm,idx)=> {
                         return(
                              <Grid item xs={1} key={idx}>
                                   <Square arg={elm} idx={idx+1}/>
                              </Grid>
                         )
                    })
               }
               <Grid item xs={4}>
                    <Square arg={'Cdigo de ritmo'}/>
               </Grid>
               {
                    ['RSR','TSR','Flutter','AcxFa','EV','BAV','MPT','Otro'].map( (elm,idx)=> {
                         return(
                              <Grid item xs={1} key={idx}>
                                   <Square  arg={elm}/>
                              </Grid>
                         )
                    })
               }
               <Grid item xs={4}>
                    <Square arg={'Cdigo respiratorio'}/>
               </Grid>
               {
                    ['21%','NCR','MMV','MRC','TOT','TQT','VMNI','VMI'].map( (elm,idx)=> {
                         return(
                              <Grid item xs={1} key={idx}>
                                   <Square  arg={elm}/>
                              </Grid>
                         )
                    })
               }
          </Grid>
     );
};
