import React,{useState} from 'react' ;
// mui
import { Box, Grid } from '@mui/material';
import Divider from '@mui/material/Divider';
// helpers
import { join} from '../../../join' ;
// import { getLastHour } from '../../../../uci/components/helpers/getLastHour' ;
// Componentes
import { ArrayGrid } from './ArrayGrid' ;
import { fieldCellsText, pullPush } from './fieldCellsText' ;


const head = (field) => {
     return(
          [`${field}`,'7-19','19-7','Total'].map( (elm,idx) =>
               <span
                    key={idx}
                    className={join( [ 'textField', 'bg_white', 'text_color_title','text_bold'] )}
               >
                    {elm}
               </span>
          )
     );
};

const sum = ( obj, array, idx ) =>{
     let res = 0 ;

     array.forEach( (elm)=>{
          if(  elm !== 'Balance Parcial' &&
               elm !== 'Balance Acumulado' &&
               elm !== 'Total'){
                    res = parseFloat( obj[`${elm}`]? obj[`${elm}`][idx] ? obj[`${elm}`][idx] : 0 : 0 ) + res  ;
               };
     });

     return( res.toFixed(1) );
};

const Column = (props) => {

     let {dataOut} = props ;

     const [data, setdata ]= useState({
          Ingresos: dataOut?.[`balance hidrico`]
                         ? dataOut[`balance hidrico`][`Ingresos`]
                              ? dataOut[`balance hidrico`][`Ingresos`]
                              : ''
                         :'',
          Egresos: dataOut?.[`balance hidrico`]
                         ? dataOut[`balance hidrico`][`Egresos`]
                              ? dataOut[`balance hidrico`][`Egresos`]
                              : ''
                         :''
     }) ;

     const fieldsIngresos =[
          'Agua-Liquidos','Alimentos VO/Ent','Agua Metablica','Dilucin Frmaco','NPTC','Sangre/ Plasma',
          'Drogas/ Flash','Sueros 1','Sueros 2','Analgecia','Total','Balance Parcial'
     ];

     const fieldsEgresos =[
          'Diuresis','Deposiciones','SNG','Vmitos','Drenaje 1','Drenaje 2','Prdidas Ins.','Ostoma',
          'Exudado/ Asp','Total','Balance Acumulado'
     ];

     const { pull } = pullPush();

     const handleBlur = ( key ) =>{

          const {field, value, idx} = pull( key==='Ingresos' ? fieldsIngresos : fieldsEgresos ) ;

          dataOut =  JSON.parse( localStorage.getItem('Data') ) ;

          dataOut[`balance hidrico`] = dataOut[`balance hidrico`] ? {...dataOut[`balance hidrico`]} : {} ;
          dataOut[`balance hidrico`][`${key}`] = dataOut[`balance hidrico`][`${key}`] ? {...dataOut[`balance hidrico`][`${key}`]}: {} ;
          dataOut[`balance hidrico`][`${key}`][field] = dataOut[`balance hidrico`][`${key}`][field] ? [...dataOut[`balance hidrico`][`${key}`][field]] : [];

          if(  field !== 'Balance Parcial' &&
               field !== 'Balance Acumulado' &&
               field !== 'Total'){

               dataOut[`balance hidrico`][`${key}`][field][idx] = value ;
               dataOut[`balance hidrico`][`${key}`][field][2] = String( parseFloat(dataOut[`balance hidrico`][`${key}`][field][0]
                                                                      ? dataOut[`balance hidrico`][`${key}`][field][0]
                                                                      : 0
                                                                 )  +
                                                                 parseFloat(dataOut[`balance hidrico`][`${key}`][field][1]
                                                                      ? dataOut[`balance hidrico`][`${key}`][field][1]
                                                                      : 0
                                                                 ) + 0) ;

               dataOut[`balance hidrico`][`${key}`][field][idx+3] = `${new Date().toLocaleString('eu-GB').split(' ')[0].split('/').join('-')}T${new Date().toLocaleString('eu-GB').split(' ')[1] }Z`;


          };

          [...new Array(3)].forEach( (_,idx)=>{
               dataOut[`balance hidrico`][`${key}`]['Total'] =  dataOut[`balance hidrico`][`${key}`]['Total']
                    ? [ ...dataOut[`balance hidrico`][`${key}`]['Total'] ]
                    : [0,0,0]  ;

               if(key === 'Ingresos'){
                    dataOut[`balance hidrico`][`${key}`]['Total'][idx] = sum( dataOut[`balance hidrico`][`${key}`], fieldsIngresos, idx )

                    dataOut[`balance hidrico`][`${key}`]['Balance Parcial'] = dataOut[`balance hidrico`][`${key}`]['Balance Parcial']
                         ? [ ...dataOut[`balance hidrico`][`${key}`]['Balance Parcial'] ]
                         : [0,0,0] ;

                    dataOut[`balance hidrico`][`${key}`]['Balance Parcial'][idx] = dataOut[`balance hidrico`][`${key}`]['Total'][idx] ;

               }else{
                    dataOut[`balance hidrico`][`${key}`]['Total'][idx] = sum( dataOut[`balance hidrico`][`${key}`],fieldsEgresos, idx )

                    dataOut[`balance hidrico`][`${key}`]['Balance Acumulado'] = dataOut[`balance hidrico`][`${key}`]['Balance Acumulado']
                         ? [ ...dataOut[`balance hidrico`][`${key}`]['Balance Acumulado'] ]
                         : [0,0,0] ;

                    dataOut[`balance hidrico`][`${key}`]['Balance Acumulado'][idx] = dataOut[`balance hidrico`][`${key}`]['Total'][idx] ;
               }
          });


          localStorage.setItem('Data',JSON.stringify(dataOut, null, 2)) ;

          setdata({
               Ingresos: {...dataOut['balance hidrico'].Ingresos},
               Egresos: {...dataOut['balance hidrico'].Egresos},
          });

     };

     return(
          <Grid
               container
               direction="row"
               justifyContent="center"
               alignItems="flex-start"

          >
               <Grid xs={6} item onBlur={ () => handleBlur('Ingresos')}>
                         <ArrayGrid  arrayObj={ head('Ingresos') } dimension={[5,2,2,3]} col={13} />
                         {
                              fieldsIngresos.map( (elm,idx)=>
                                   <ArrayGrid
                                        key={idx}
                                        arrayObj={ fieldCellsText({field:elm, cells:3, data:data.Ingresos,typ:'text', tabIndex:`${elm}:${idx}` }) }
                                        dimension={[5,2,2,3]}
                                        col={13}
                                   />
                              )
                         }
               </Grid>

               <Grid xs={6} item onBlur={ () => handleBlur('Egresos')}>
                         <ArrayGrid  arrayObj={ head('Egresos') } dimension={[5,2,2,3]} col={13} />
                         {
                              fieldsEgresos.map( (elm,idx)=>
                                   <ArrayGrid
                                        key={idx}
                                        arrayObj={ fieldCellsText ({field:elm, cells:3, data:data.Egresos, typ:'text', tabIndex:`${elm}:${idx}`} ) }
                                        dimension={[5,2,2,3]}
                                        col={13}
                                   />
                              )
                         }
               </Grid>
          </Grid>
     )
};

export const BalanceHidrico = ( {dataOut} ) => {

     return(
          <Box>
               <div
                    className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
               >
                    <Divider textAlign="center">
                         Balance Hdrico (ml)
                    </Divider>
               </div>

               <Column  dataOut={ dataOut } />

          </Box>
     );
};
