import React, {useState} from 'react';
//mui
import { Box } from '@mui/material';
//Componentes
import { RadioButton } from './RadioButton' ;
import { TextInput } from './TextInput' ;



export const CheckBoxTextInput = ({
          // dataOut,
          field1='',
          field2='',
          opc = [],
          help=false
     }) => {

     let data = JSON.parse( localStorage.getItem('Data') );

     const setValue = useState(
          data?.[`${field1}`] ?  data[`${field1}`] : {}
     )[1];

     const handleBlurText = () => {
          data = JSON.parse( localStorage.getItem('Data') );
          localStorage.setItem('Data',JSON.stringify(data, null, 2)) ;
     };

     const handleChange = () => {
          if(  data[`${field1}`]=== 'No' ) delete data[`${field2}`] ;

          data = JSON.parse( localStorage.getItem('Data') );
          setValue( data[`${field1}`] );

          localStorage.setItem('Data',JSON.stringify(data, null, 2)) ;
     };

     const Field = () => {
          return(
               <TextInput
                    onBlur={ ()=> handleBlurText() }
                    field={field2}
                    width='20'
                    dataOut={data}
                    disable={ data?.[`${field1}`] ?  !(data[`${field1}`] === 'Si') : true }
               />
          )
     };

     return(
          <Box onChange={ () => handleChange() }>
               <RadioButton  dataOut={data} field={field1} fields={opc} renderField={Field} help={help}/>
          </Box>
     );
};
