





import {ifExits} from './ifExits'; // idExits( 'prop', obj )

export const loadMedicationDispense = ( data, id, identifier, arrayElements ) => {

     return(
          {
               resourceType: "MedicationDispense",
               identifier: [
                    {
                         use:"usual",
                         value: identifier
                    }
               ],
               status : "in-progress",
               subject: {
                    reference:`Patient/${id}`
               },
               category: {
                    text: ifExits(arrayElements[1], data)
               },
               medicationReference:{
                    identifier: {
                         use: "usual",
                         value: ifExits(arrayElements[0], data)
                    },
                    display : ifExits(arrayElements[0], data)
               },
               quantity:{
                    value : parseFloat( ifExits(arrayElements[2], data).split(' ').slice(0) ),
                    unit  : ifExits(arrayElements[2], data).split(' ').slice(1).join(' ')
               },
               daysSupply:{
                    value : parseFloat( ifExits(arrayElements[3], data).replace(/[^0-9]/g, "")===''
                                             ? 0
                                             : ifExits(arrayElements[3], data).replace(/[^0-9]/g, "")
                                        ),
                    unit  : ifExits(arrayElements[3], data).replace(/[^a-zA-Z]/g, "")
               },
               whenHandedOver: ifExits(arrayElements[4], data)
          }
     );

}
