import React, {useState } from 'react';

import LoadingButton from '@mui/lab/LoadingButton';
import SaveIcon from '@mui/icons-material/Save';
import {sendToServer} from '../../../recursosFHIR/sendToServer.js' ;
import { join} from '../../../join' ;

export const LoadButton = () => {

     const [loading, setLoading] = useState(false);
     const [resp, setResp] = useState('Guardar') ;

     const handleClick = (state) => {
          setLoading(state);
     };

     // const data = dataOut ;

     const handleSave = async() =>{
          handleClick(true);
          // localStorage.setItem('Data', JSON.stringify( data, null, 2) ) ;

          // await send( JSON.parse( localStorage.getItem('Data') ) ) ;
          let dta = JSON.parse( localStorage.getItem('Data') ) ;
          console.log( dta );
          const [msn, bundle]  = await sendToServer( dta, dta.Fecha );


          console.log( msn, bundle );

          if( msn === 'OK'){
               setResp( 'Guardado' )
          }else{
               const idx = msn.split('entry[')[1].slice(0,10).replace(/[^0-9]/g,'')

               console.log(idx);

               setResp( `${msn.split('[')[0]} : ${bundle.entry[idx].resource.identifier[0].value}` );
          };
               
          handleClick(false);
     };

     return(
          <>
               <LoadingButton
                    loading={loading}
                    loadingPosition="start"
                    startIcon={<SaveIcon />}
                    variant="contained"
                    onClick={ () =>  handleSave()  }
                    className={ join(['title2','text_white','text_bold','text_center','font_monserrat','fullwidth','my_2']) }
               >

                    <p>{ loading ?  'Guardando' : resp }</p> 

               </LoadingButton>
          </>
     )
};
