

import {ifExits} from './ifExits'; // idExits( 'prop', obj )

export const loadObservationValueQuantity = ( data, id, identifier, code, valueItem, units) => {

     return(
          {
               resourceType: "Observation",
               identifier: [
                    {
                         use: "official",
                         value: `${identifier}`
                    }
               ],
               status: "registered",
               code: {...code},
               valueQuantity:{
                    value : parseFloat( ifExits( valueItem , data )=== '--' ? 0 :  ifExits( valueItem , data )  ),
                    unit: units
               },
               subject: {
                         reference: `Patient/${id}`
                    }
          }
     );
};
