import {useEffect, useState} from 'react' ;
// Material UI
import { Box, Grid } from '@mui/material';
// Estilos
import { Stl } from '../../Stl';
import { MediaQuery } from '../../MediaQuery' ;
// Componentes
import { TextInput } from './helpers/TextInput';
import { CheckBoxTextInput } from './helpers/CheckBoxTextInput' ;
import { RadioButton } from './helpers/RadioButton';

// helpers
import { join} from '../../join' ;

// Descripciones para Hint Component
import { descripciones } from './helpers/descripciones' ;

export const PersonalInformation = ({change,set}) => {

     const classes = Stl();
     const {_xl,_lg} = MediaQuery();

     const [dta, setDta] = useState(
          JSON.parse( localStorage.getItem('Data') )
     ) ;


     useEffect( ()=>{
          setDta(JSON.parse( localStorage.getItem('Data') ))
     },[change]);


     return(
          <Box
               pt={_xl ? 3 : 2}
               px={_xl ? 3 : 2}
               mt={_xl ? 3 : 2}
               className={[classes.bg_white].join(' ')}
               style={{height: _xl ? '1130px':_lg?'970px':'843px'}}
          >
               <Grid
                    container
                    direction="row"
                    justifyContent="space-around"
                    alignItems="flex-start"
                    spacing={1}
                    pr={1}
               >
                    <Grid xs={6} item container>
                         <div className={join([ 'title3', 'text_color_title', 'text_bold', 'fullwidth','mt'])}>
                              Datos personales:
                         </div>
                         <TextInput field={'UCI'} width={_xl? '12':_lg? '12' :'10'} dataOut={dta} />
                         <TextInput field={'UTIQ'} width={_xl? '12':_lg?'12' :'10'} dataOut={dta} disable={true}/>
                         <TextInput field={'UTIM'} width={_xl? '12':_lg?'12' :'10'} dataOut={dta} disable={true}/>
                         <TextInput field={'Fecha'} width={'40'} dataOut={dta} type={'date'} setChange={set}/>
                         <TextInput field={'RUT'} width={_lg?'27':'20'} dataOut={dta} />
                         <TextInput field={'Nombres y apellidos'} fullwidth={true} dataOut={dta}/>
                         <TextInput field={'Edad'}  width={_lg? '12' :'10'} dataOut={dta} type='text'/>
                         <TextInput field={'Peso'} width={_lg?'12' :'10'} dataOut={dta} type='text'/>
                         <TextInput field={'Sexo'} width={_lg?'12' :'10'} dataOut={dta} />
                         <TextInput field={'Rh'} width={_lg? '12' :'10'} dataOut={dta} />
                         <TextInput field={'Ala'} width={_lg? '12':'10'} dataOut={dta} />
                         <TextInput field={'Cama'} width={_lg? '12':'10'} dataOut={dta} />
                    </Grid>
                    <Grid xs={6} item container>
                         <div className={join([ 'title3', 'text_color_title', 'text_bold', 'fullwidth','mt'])}>
                              Fecha de ingreso:
                         </div>
                         <TextInput field={'Da'} width={_lg? '12':'10'} dataOut={dta}  type={'text'}/>
                         <TextInput field={'Mes'} width={_lg? '12':'10'} dataOut={dta} type={'text'}/>
                         <TextInput field={'Ao'} width={_lg? '12':'10'} dataOut={dta} type={'text'}/>
                         <TextInput field={'Cuenta corriente'} fullwidth={true} dataOut={dta} type={'text'}/>
                         <TextInput field={'Das Hosp.'}  width={_lg? '12':'10'} dataOut={dta} type={'text'}/>
                         <TextInput field={'Das UPC'} width={_lg? '12':'10'} dataOut={dta} type={'text'}/>
                         <TextInput field={'Das Post.'} width={_lg? '12':'10'} dataOut={dta} type={'text'}/>
                         <TextInput field={'Estado'} width={_xl?'13':_lg?'13':'11'} dataOut={dta} />
                         <TextInput field={'Estado de bienestar (0-3)'}  width={_xl?'26':_lg?'26':'23'} dataOut={dta} type='text'/>
                         <TextInput field={'Sala'} width={_xl ?'20' : _lg?'20':'17'} dataOut={dta} />
                         <TextInput field={'Talla'} width={_xl?'19' :_lg?'19':'17'} dataOut={dta} type='text'/>
                    </Grid>
               </Grid>

               <Grid
                    container
                    direction="row"
                    justifyContent="space-around"
                    alignItems="flex-start"
                    spacing={1}
               >
                    <Grid xs={6} item container>
                              <TextInput field={'Diagnostico'} fullwidth={true} dataOut={dta} mltLine={true} maxRow={6}/>
                    </Grid>

                    <Grid xs={6} item container>
                         <TextInput field={'Equipo tratante'}  fullwidth={true} dataOut={dta} mltLine={true} maxRow={6}/>
                    </Grid>

                    <Grid xs={12} item container>
                              <TextInput field={'Motivo Consulta'} fullwidth={true} dataOut={dta} mltLine={true} maxRow={_xl?5:8}/>
                    </Grid>

                    <Grid xs={6} item container  mt={2}>
                         <Box   className={join([ 'title3','text_color_title', 'text_bold', 'fullwidth'])}>
                              Brazalete de Indentificacin
                         </Box>
                         <Box ml={2} pt={1} style={{display:'flex',flexDirection:'row', flexWrap:'nowrap'}}>
                              <RadioButton dataOut={dta} field='Turno Largo' fields={['Si','No']} help={descripciones['TL']}/>
                              <RadioButton dataOut={dta}  field='Turno Noche' fields={['Si','No']}  help={ descripciones['TN']}/>
                         </Box>
                    </Grid>

                    <Grid xs={6} item container>
                         <CheckBoxTextInput field1='Brazalete de Alergas' field2='Alrgeno' opc={['Si','No']} dataOut={dta}/>
                    </Grid>

               </Grid>

          </Box>
     );
};
