import React,{useState } from 'react';
// Material ui
import {Box} from '@mui/material';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControl from '@mui/material/FormControl';
import Checkbox from '@mui/material/Checkbox';
// Estilos
import { Stl } from '../../../Stl';
import {MediaQuery} from '../../../MediaQuery'

import { Hint } from './Hint';
// date from Azores
import { getLastHour }  from '../../../../uci/components/helpers/getLastHour';

export const RadioButton = ({
          field='',
          displayName=true,
          fields=['1'],
          disable=false,
          dataOut,
          typ='radio',
          save=true,
          renderField=()=> <></>,
          help=''
     }) => {

     const classes = Stl();
     const {_xl,_lg} = MediaQuery();

     let data = save 
                    ?  JSON.parse( localStorage.getItem('Data') ) 
                    : dataOut 
                         ? dataOut
                         : {[`${field}`] : ''};

     const [fieldValue, setFieldValue] = useState({
          [`${field}`]:localStorage.getItem('Data')
               ? JSON.parse( localStorage.getItem('Data'))?.[`${field}`]
                    ? JSON.parse( localStorage.getItem('Data'))[`${field}`]
                    : data?.[`${field}`] ? data[`${field}`] : ''
               : data?.[`${field}`] ? data[`${field}`] : ''
     });

     const handleChangeRadio = (value) => {

          data = save 
                    ?  JSON.parse( localStorage.getItem('Data') ) 
                    : dataOut 
                         ? dataOut
                         : {[`${field}`] : ''};

          data[`${field}`] = value ;
          data[`${field}_version`] = getLastHour('date',true);

          fieldValue[`${field}`] = value ;

          if(save) {
               localStorage.setItem('Data',JSON.stringify(data, null, 2)) ;
          }
          setFieldValue({
               ...fieldValue
          });
     };

     return(
          <Box pr={2} mt={2}>
               <FormControl
                    style={{width:'100%'}}
               >

                    <RadioGroup
                         aria-labelledby="demo-radio-buttons-group-label"
                         defaultValue=""
                         name={field}
                    >
                         <span
                              className={[classes.title3, classes.text_color_title,classes.text_bold].join(' ')}
                              style={{marginRight:'1rem', display: 'flex', alignItems: 'center', marginTop: help ? '0rem': '0.2rem'}}
                         >
                              { displayName? `${field}` : ''}

                              <Hint help={help}/>
                         </span>

                         <Box pl={2} mt={1}
                              style={{
                                   display:'flex',
                                   flexWrap:'nowrap',
                                   justifyContent:'space-between',
                                   alignItems: 'center',
                                   border:'1px solid rgb(100,100,100,30%)',
                                   borderRadius: '10px'
                              }}
                         >

                              {
                                   fields.map((elm,idx) => {
                                        return(
                                             <span key={idx} className={[ classes.text_gray2,classes.textField, classes.font_monserrat].join(' ')}
                                                  style={{display:'inline-block'}}
                                             >
                                                  {elm}
                                                  {typ === 'radio' ?
                                                       <Radio
                                                            value={elm}
                                                            checked={fieldValue[`${field}`]===elm}
                                                            onChange={(event) => { 
                                                                 event.preventDefault()
                                                                 handleChangeRadio(elm) 
                                                            }}
                                                            sx={{ '& .MuiSvgIcon-root': {fontSize:_xl?50:_lg?25:20} }}
                                                       />
                                                       :
                                                       <Checkbox
                                                            value={elm}
                                                            sx={{ '& .MuiSvgIcon-root': {fontSize:_xl?50:_lg?25:20} }}
                                                       />
                                                  }
                                             </span>
                                        )
                                   })
                              }
                              { renderField() }
                         </Box>
                    </RadioGroup>
               </FormControl>
          </Box>
     );
};
