import React from 'react' ;
// mui
import { Grid } from '@mui/material';
// descripciones
import { Hint } from './Hint';
import { descripciones } from './descripciones';

export const ArrayGrid = ({dimension, arrayObj, col } ) => {

     return(
          <Grid
               container
               direction="row"
               justifyContent="center"
               alignItems="center"
               columns={col}
          >
               {
                    arrayObj.map( (elm,idx) =>{
                         return(
                              <Grid
                                   xs={dimension[idx]} item key={idx} justifyContent="center"
                              >
                                   <div style={{display:'flex', alignItems:'center'}}>
                                        {elm}
                                        <Hint help={descripciones[elm] ? descripciones[elm] : false }/>
                                   </div>
                              </Grid>
                         )

                    })
               }
          </Grid>
     )
};
