import React from 'react';
// Material ui
import { Box, Grid } from '@mui/material';
// Estilos
import { Stl } from '../../../Stl';
// Componentes
import { TextInput } from './TextInput' ;
import { RadioButton } from './RadioButton' ;

export const MltObjText = ({
          field='',
          fields,
          width=100,
          type='text',
          disable=false,
          // dataOut,
          mltLine=false,
          maxRow=1
          }) => {

     const classes = Stl();

     let data = JSON.parse( localStorage.getItem('Data') );

     const handleBlur = () => {

          data = JSON.parse( localStorage.getItem('Data') );

          data[`${field}`]={evalLargo:evalLargo , evalNoche:evalNoche } ;

          if(data[`${field}`].length< 1)  delete data[`${field}`]
          localStorage.setItem('Data',JSON.stringify(data, null, 2)) ;

     };

     const evalLargo = data?.[`${field}`] ? data[`${field}`]['evalLargo'] ?  data[`${field}`]['evalLargo'] : {} : {} ;

     const evalNoche = data?.[`${field}`] ? data[`${field}`]['evalNoche'] ?  data[`${field}`]['evalNoche'] : {} : {} ;

     const renglonTexto = (title,idx) =>{
          return(
               <Grid
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center"
                    className={classes.bg_white}
                    spacing={1}
                    key={idx}
                    columns={15}
               >
                    <Grid item xs={3}>
                         <Box className={[ classes.text_gray2, classes.title3, classes.font_monserrat].join(' ')}>
                              {title}
                         </Box>
                    </Grid>
                    <Grid item xs={5}>
                         <div  style={{paddingTop:'1rem'}}>
                              <TextInput field='Puntaje' dataOut={!idx ? evalLargo : evalNoche} fullwidth save={false}/>
                         </div>
                    </Grid>
                    <Grid item xs={7} justifyContent="space-around" alignItems="center">
                         <RadioButton field='medida' displayName={false} fields={fields} dataOut={!idx ? evalLargo : evalNoche} save={false}/>
                    </Grid>

               </Grid>
          );
     };

     
     return(
          <Box
               className={[classes.bg_white].join(' ')}
               pt={2}
               sx={ {width:`${width}%`, m:1 } }
               noValidate
               autoComplete="off"
               onBlur={handleBlur}
          >
               <Grid
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center"
                    className={classes.bg_white}
                    spacing={1}
               >
                    <Grid item xs={2}>
                         <Box className={[classes.title2, classes.bg_white, classes.text_color_title, classes.text_bold].join(' ')}>
                              {field}
                         </Box>
                    </Grid>

                    <Grid item xs={10}>
                         {
                              ['Eval. Turno de  Largo', 'Eval. Turno de Noche'].map( (elm,idx) =>{
                                        return(
                                             renglonTexto(elm,idx)
                                        )
                              })
                         }
                    </Grid>
               </Grid>
          </Box>
     );
};
