import React from 'react';
//mui
import { Box } from '@mui/material';
import Divider from '@mui/material/Divider';

//Componentes

import { MltLineObj } from './MltLineObj' ;

// helpers
import { join} from '../../../join' ;

export const Ingresos = ( {jornada, dataOut} ) =>{

     const data = dataOut ;

     return(
          <Box
               my={3}
          >
               <div
                    className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
               >
                    <Divider textAlign="center">
                         {`Ingresos/${jornada}`}
                    </Divider>
               </div>

               <MltLineObj
                    dataOut={data}
                    field={`Balance${jornada}`}
                    fields={[ 'Hora','Ing. oral/enteral','Cantidad','Ing. parental','Inicio','Termin','Pediendiente']}
                    size={[3,4,3,3,2,2,3]}
                    columns={20}
               />
          </Box>
     );
};
