
import { getLastHour }  from '../../../../uci/components/helpers/getLastHour';

export const calcHours = (label,data) => {


     const dateObj = data?.[label] === 'Si' ? data?.[`${label}_version`] : getLastHour('date',true) ;
     const dateNow = getLastHour('date',true) ;
     const dif = new Date(dateNow) - new Date(dateObj);

     return(
          dif === 0
               ? 0
               : (new Date(dif)/1000/60/60).toFixed(2)
     )
};
