
import {ifExits} from './ifExits'; // idExits( 'prop', obj )
import {addDate}from './addDate' ;

export const loadDiagnosticReport = ( data, id, identifier, code ) => {

     return(
          {
               resourceType : "DiagnosticReport",
               identifier : [
                    {
                         use: "official",
                         value: `${identifier}`
                    }
               ],
               status : "registered",

               code :  {...code},
               subject: {
                    reference: `Patient/${id}`
               },
               effectiveDateTime : `${addDate(data)[0]}-${addDate(data)[1]}-${addDate(data)[2]}`,
               resultsInterpreter : [
                    {
                         "display": ifExits( 'Motivo Consulta', data)
                    }
               ],
               result:[
                    {
                         "display": ifExits('Estado', data)
                    }
               ],
               conclusion : ifExits('Diagnostico', data)
          }
     );
};
