import {ifExits} from './ifExits'; // idExits( 'prop', obj )

export const loadObservationValueInteger = ( data, id, identifier, code, valueItem ) => {

     return(
          {
               resourceType: "Observation",
               identifier: [
                    {
                         use: "official",
                         value: `${identifier}`
                    }
               ],
               status: "registered",
               code: {...code},
               valueInteger :  Number.isNaN( parseFloat(ifExits( valueItem , data ))) ? 0 :  parseFloat( ifExits( valueItem , data )  ),
               subject: {
                    reference: `Patient/${id}`
               }
          }
     );
};
