import React,{useState} from 'react';
//mui
import { Box, Grid } from '@mui/material';
//Componentes
import { TextInput } from './TextInput' ;
import { TextInputOpt } from './TextInputOpt' ;
// helpers
import { join} from '../../../join' ;
import { getLastHour } from '../../../../uci/components/helpers/getLastHour';

// descripcion
// import { descripciones } from './descripciones';

export const MltLineObj = ({
          // dataOut,
          field='',
          fields=[],
          size=[],
          columns=12
     }) => {

     let data  = JSON.parse( localStorage.getItem('Data') );

     // Llenado del objeto, si existe
     const [dtaArray , setDtaArray] = useState({
          [`${field}`]: data?.[field] ? data[field] : [] ,
          det:[]
     });


     const [len, setLen] = useState({
          value: (dtaArray[`${field}`] ? dtaArray[`${field}`].length : 0 ) + 1
     });

     const opc = [' ','1 vez','2h','4h','6h','8h','12h','24h','SOS PRN'] ;

     const TextLine = ({obj,idx,fields,help}) =>{

          return(
               fields.map( (item,idx1)=>{
                    return(
                         <Grid key={idx1} item xs={ size[idx1] } onBlur={ ()=>handleBlur(obj,idx,idx1) } >
                              {
                                   item !== 'Frecuencia'
                                   ? <TextInput  field={ item } dataOut={ obj }  save={false} fullwidth  />
                                   : <TextInputOpt field={ item } dataOut={ obj }  save={false} fullwidth opt={ opc } blur={()=>handleBlur(obj,idx,idx1)} />
                              }
                         </Grid>
                    );
               })
          );
     };

     const noEmpty = ( obj ) =>{
          let _noempty = fields.filter( (elm) => {
                    return( (obj[`${elm}`] ? obj[`${elm}`].length > 0 : 0) !== 0 )
               });
          return(
               fields.length === _noempty.length
          );
     };

     const empty = ( obj ) =>{
          let _empty = fields.filter( (elm) => {
                    return( (obj[`${elm}`] ? 0 : 1) !== 0 )
               });
          return(
               fields.length === _empty.length
          );
     };

     const suma = ( obj ) =>{
          return(
               fields.reduce( (ant,act) => ant + obj[`${act}`],' ')
          );
     };

     const handleBlur = ( dta, idx, idxFila ) =>{

          data = JSON.parse( localStorage.getItem('Data') );

          if( idxFila+1 >= fields.length ){
               if( noEmpty(dta) && ( dtaArray.det.indexOf( suma(dta) ) < 0 ) ){

                    dtaArray.det[`${idx}`] = suma(dta)
                    dtaArray[`${field}`][`${idx}`] = dta //.replace(/[ ]/g,'')
                    dtaArray[`${field}`][`${idx}`]['fecha'] = getLastHour('date', true) ;
                    data[field] = dtaArray[`${field}`] ;
                    console.log("mltln - grd1", dta);
                    localStorage.setItem('Data',JSON.stringify(data, null, 2)) ;

                    setDtaArray({
                              [`${field}`]: data[field] ,
                              det:[...dtaArray.det]
                         });

                    setLen({ value: data[field].length + 1});

               }else if( empty(dta) ){

                    if( dtaArray[`${field}`].length > 1  ){
                         dtaArray[`${field}`].splice(idx,1)
                         dtaArray.det.splice(idx,1)
                         setLen({ value: data[field].length + 1});
                    }else{
                         setDtaArray({
                              [`${field}`] : [],
                              det : []
                         });
                         setLen({ value:1 });
                    };

                    data[field] = dtaArray[`${field}`] ;

                    if(data[field].length< 1)  delete data[field]
                    console.log("mltln - grd2");
                    localStorage.setItem( 'Data',JSON.stringify(data, null, 2) ) ;

                    setDtaArray({
                              [`${field}`]: data[field] ,
                              det:[...dtaArray.det]
                         })

               }else if( ( dtaArray.det.indexOf( suma(dta) ) >= 0 ) ){
                    if( dtaArray.det.indexOf( undefined ) >= 0 ){
                         dtaArray[`${field}`].splice(idx,1)
                    }

                    setDtaArray({
                              [`${field}`]: dtaArray[`${field}`] ,
                              det:[...dtaArray.det]
                         })

                    setLen({
                         value: (dtaArray[`${field}`] ? dtaArray[`${field}`].length : 0 ) + 1
                    })
               };
          };
     };

     const vacios = () =>{
          const obj = {};

          fields.forEach( (elm) => {
                    obj[`${elm}`] = []
          });

          return(
               {...obj}
          );
     };

     return(
          <Box style={{width:'100%'}}>

               <Grid
                    container
                    direction="row"
                    justifyContent="flex-start"
                    alignItems="baseline"
                    columns={columns}
               >

                    <Box
                         className={join(['title3','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_1'])}
                    >
                         Tabla Dinmica
                    </Box>
                    {
                         [...new Array( len.value )].map( (_,idx) => {
                              return(
                                   <TextLine
                                        key={idx}
                                        obj={ dtaArray[`${field}`] ? dtaArray[`${field}`][idx] ? dtaArray[`${field}`][idx]: vacios(): vacios() }
                                        idx={idx}
                                        fields={fields}
                                        help={ idx===0 }
                                   />
                              )
                         })
                    }
               </Grid>

          </Box>
     );
};
