
import { loadPatient } from './helpers/loadPatient' ;
import { loadAccount } from './helpers/loadAccount' ;
import { loadBasic } from './helpers/loadBasic' ;
import { loadObservationValueString } from './helpers/loadObservationValueString' ;
import { loadObservationValueQuantity } from './helpers/loadObservationValueQuantity' ;
import { loadObservationValueInteger }  from './helpers/loadObservationValueInteger' ;
import { loadDiagnosticReport }  from './helpers/loadDiagnosticReport' ;
import { loadCommunication }  from './helpers/loadCommunication' ;
import { loadMedicationDispense }  from './helpers/loadMedicationDispense' ;
import { ifExits }  from './helpers/ifExits' ;
import { addToken } from '../../uci/components/helpers/token';

import { LoadBundle } from './helpers/LoadBundle' ;
import { codes }  from './helpers/codes' ;

import {resourceServer }  from  '../../uci/env' ;

export const sendToServer = async ( data, date ) => {

     const { urlServer,
          requestOptionsPost,
          requestOptionsGet,
          requestOptionsPut } = resourceServer ;

     const objPatient = loadPatient(data) ;

     const patGet = {} ;
     let idPatient = 0 ;

     patGet.respGet = await fetch( `${urlServer}Patient?identifier=${objPatient.identifier[0].value}`, addToken(requestOptionsGet) )
     patGet.objGet = await patGet.respGet.json();

     if( await patGet.objGet.total > 0  ){ // Verificar si existe el paciente: Los objetos se actualizan
          console.log("Actualizar Patient");
          idPatient =  patGet.objGet.entry[0].resource.id ;
          objPatient.id = idPatient ;

          requestOptionsPut.body = JSON.stringify( objPatient );

          const config = addToken(requestOptionsPut) ;
          config.headers.append("Content-Type", "application/json");

          await fetch( `${urlServer}Patient/${idPatient}`, config ) ;
          // console.log(  await respPut.json() );

     }else{ // El paciente no existe: Se crean los objetos
          console.log("Crear Patien");
          requestOptionsPost.body = JSON.stringify( objPatient );

          const config = addToken(requestOptionsPost) ;
          config.headers.append("Content-Type", "application/json") ;

          await fetch( `${urlServer}Patient`, config ) ;
          // Obtener ID del paciente
          patGet.respGet = await fetch( `${urlServer}Patient?identifier=${objPatient.identifier[0].value}`, addToken(requestOptionsGet) )
          patGet.objGet = await patGet.respGet.json();
          idPatient =  patGet.objGet.entry[0].resource.id ;
     } ;

     const bundle = {
          resourceType : "Bundle",
          type : "batch",
          entry:[]
     };
     // medir el tiempo de procesado de las solicitudes
     let t1 = new Date();

     // Construccion de recursos con similar estructura
     let recursos = [
         { resource:'Account',obj:data, identifier:`Account`, makeResource:loadAccount, makeArgs:[], daily:false },
         { resource:'Basic',obj:data, identifier:`diasRegistro`, makeResource:loadBasic, makeArgs:[['Das Hosp.','Das UPC','Das postOp.']], daily:false },
         { resource:'Observation',obj:data, identifier:`Rh-Bld`, makeResource:loadObservationValueString, makeArgs:[codes().Rh , 'Rh'], daily:false },
         { resource:'Observation',obj:data, identifier:`body-weigth`, makeResource:loadObservationValueQuantity, makeArgs:[codes().Weigth, 'Peso', 'Kg'], daily:false },
         { resource:'Observation',obj:data, identifier:`talla-cm`, makeResource:loadObservationValueInteger, makeArgs:[codes().Generic, 'Talla'], daily:false },
         { resource:'DiagnosticReport',obj:data, identifier:`clinicInfo`, makeResource:loadDiagnosticReport, makeArgs:[codes().Generic ], daily:false },
         // { resource:'Communication',obj:data, identifier:`Diagnostico`, makeResource:loadCommunication, makeArgs:[['Diagnostico']] },
         // { resource:'Communication',obj:data, identifier:`MotivoConsulta`, makeResource:loadCommunication, makeArgs:[['Motivo consulta']] },
         { resource:'Communication',obj:data, identifier:`equipoTrt`, makeResource:loadCommunication, makeArgs:[['Equipo tratante']], daily:false },
         { resource:'Basic',obj:data, identifier:`brazaleteIdentificacion`, makeResource:loadBasic, makeArgs:[[ 'Turno Largo','Turno Noche']], daily:true },
         { resource:'Basic',obj:data, identifier:`brazaleteAlergias`, makeResource:loadBasic, makeArgs:[[ 'Brazalete de Alergas','Alrgeno']], daily:false },
         { resource:'Basic',obj:data, identifier:`obsEvolucion`, makeResource:loadBasic, makeArgs:[['Cardiovascular-Da','Respiratorio-Da','Neurologco-Da','Eliminacin-Da','Renal-Da','Alimentacin-Da','Post-Quirrgico-Da','Cardiovascular-Noche','Respiratorio-Noche','Renal-Noche','Neurologco-Noche','Alimentacin-Noche','Eliminacin-Noche','Post-Quirrgico-Noche']], daily:true },
         { resource:'Basic',obj:ifExits(['Riesgo de Cada','evalLargo'],data) , identifier:`riesgoCaidaLargo`, makeResource:loadBasic, makeArgs:[['Puntaje','medida']], daily:true },
         { resource:'Basic',obj:ifExits(['Riesgo de Cada','evalNoche'],data), identifier:`riesgoCaidaNoche`, makeResource:loadBasic, makeArgs:[['Puntaje','medida']], daily:true },
         { resource:'Basic',obj:ifExits(['Riesgo de UPP','evalLargo'],data), identifier:`riesgoUPPLargo`, makeResource:loadBasic, makeArgs:[['Puntaje','medida']], daily:true },
         { resource:'Basic',obj:ifExits(['Riesgo de UPP','evalNoche'],data), identifier:`riesgoUPPNoche`, makeResource:loadBasic, makeArgs:[['Puntaje','medida']], daily:true },
         { resource:'Basic',obj:data, identifier:`riesgoEnfTrombo`, makeResource:loadBasic, makeArgs:[['Riesgo Enf. Tromboemblica']], daily:false },
         { resource:'Basic',obj:data, identifier:`indMedicas`, makeResource:loadBasic, makeArgs:[[ 'Aislamiento','Reposo','Rgimen','Profilxis Mecnica (TEV)','Especifique','KTR','Destete']], daily:false },
         { resource:'Basic',obj:data, identifier:`Medicacion`, makeResource:loadBasic, makeArgs:[[ 'Profilaxis Farmacologica','Contenciones','Nombre Doctor','Cdigo']], daily:false },
         { resource:'Basic',obj:data, identifier:`MantencionDisp.`, makeResource:loadBasic, makeArgs:[[ 'TOT','TQT','CVC','CUP','MPT','CF','CHD','CSG','SNG','SNE','SNY','LA','VVP','CPD','S.Foley','Drenajes']], daily:false },
         { resource:'Communication',obj:data, identifier:`alarmaDia-resp`, makeResource:loadCommunication, makeArgs:[['Responsable (DA):']], daily:true },
         { resource:'Basic',obj:data, identifier:`alarmaDia-hor`, makeResource:loadBasic, makeArgs:[['Responsable (DA):']], daily:true },
         { resource:'Basic',obj:data, identifier:`alarmaNoche-resp`, makeResource:loadBasic, makeArgs:[['Responsable (NOCHE):']], daily:true },
         { resource:'Basic',obj:data, identifier:`alarmaNoche-hor`, makeResource:loadBasic, makeArgs:[['Hora (NOCHE):']], daily:true },
         { resource:'Basic',obj:data, identifier:`examenFiscNombTurnLarg`, makeResource:loadBasic, makeArgs:[['NOMBRE: Enfermero Turno Largo']], daily:true },
         // { resource:'Basic',obj:data, identifier:`examenFiscParam`, makeResource:loadBasic, makeArgs:[['TOT','TQT','CVC','MPT','CF','CSG','SNG','SNY','LA','VVP','CPD','S.Foley','Drenajes']] },
         { resource:'Basic',obj:data, identifier:`examenFiscNombTurnNoct`, makeResource:loadBasic, makeArgs:[['NOMBRE: Enfermero Turno Nocturno']], daily:true },
         { resource:'Basic',obj:ifExits(['ProgramacionEnfer','izq'],data), identifier:`ProgramacionEnferIzq`, makeResource:loadBasic, makeArgs:[['BD','BI','PD','PI','Trax','NA','NOMBRE: Enfermero Turno Diurno']], daily:true },
         { resource:'Basic',obj:ifExits(['ProgramacionEnfer','der'],data), identifier:`ProgramacionEnferDer`, makeResource:loadBasic, makeArgs:[['BD','BI','PD','PI','Trax','NA','NOMBRE: Enfermero Turno Nocturno']], daily:true },

     ];

     // Construccion de recursos de medicaciones
     if( data.indicaciones ){
          const medicacion = data.indicaciones.map( (item, idx) => {
               return(
                    { resource:'MedicationDispense',obj:item, identifier:`Medicacion${idx+1}`, makeResource:loadMedicationDispense, makeArgs:[[ 'Medicamentos','Va','Dosis','Frecuencia','fecha']], daily:false }
               );
          });
          recursos = recursos.concat( medicacion );
     };

     // Construccion  de recursos de tablas
     [
          {resource: 'Basic', obj:ifExits(['alarmas','DA'],data), make:loadBasic, identifier:'alarmaDia', daily:true },
          {resource: 'Basic', obj:ifExits(['BalanceDA'],data), make:loadBasic, identifier:'ingresosDia', daily:true },
          {resource: 'Basic', obj:ifExits(['alarmas','NOCHE'],data), make:loadBasic, identifier:'alarmaNoche', daily:true } ,
          {resource: 'Basic', obj:ifExits(['balance hidrico','Egresos'],data), make:loadBasic, identifier:'balancHidEgresos', daily:true },
          {resource: 'Basic', obj:ifExits(['balance hidrico','Ingresos'],data), make:loadBasic, identifier:'balancHidIngresos', daily:true },
          {resource: 'Basic', obj:ifExits(['examen fisico'],data), make:loadBasic, identifier:'examenFisico', daily:true } ,
          {resource: 'Basic', obj:ifExits(['ProgramacionEnfer'],data), make:loadBasic, identifier:'ProgramacionEnfer', daily:true } ,

     ].forEach( (item1) => {
          let arrayResources = [] ;
          if( item1.obj !== '' ){
               // conversin de objeto a array de objetos
               if( !Array.isArray(item1.obj) ){

                    let tempArray = Object.keys(item1.obj).map( (label) => {
                         return (
                              { [label]:item1.obj[label] }
                         );
                    });
                    item1.obj = tempArray ;
               };

               item1.obj.forEach( (item2, idx) => {
                    if( item2 !== null ){
                         if( item2[Object.keys(item2)[0]] !== "" ){
                              if( Object.keys(item2).length > 1){
                                   arrayResources.push( // para tablas dinamicas
                                        {    resource:item1.resource,
                                             obj: item2,
                                             identifier:`${item1.identifier}:${idx}`,
                                             makeResource: item1.make ,
                                             makeArgs:[ Object.keys(item2) ]
                                        }
                                   )
                              }else{
                                   arrayResources.push( // para tablas estaticas
                                        {    resource:item1.resource,
                                             obj:item2[Object.keys(item2)[0]] ,
                                             identifier:`${item1.identifier}:${Object.keys(item2)[0].replace(/ /g,'').replace(/[^a-zA-Z 0-9 ]/g, "")}`,
                                             text:`${item1.identifier}:${Object.keys(item2)[0]}`,
                                             makeResource: item1.make ,
                                             makeArgs:[ Object.keys( item2[Object.keys(item2)[0]] ) ]
                                        }
                                   )
                              };
                         };
                    };
               });
               recursos = recursos.concat( arrayResources );
          };
     });

     // Construcion de los request con recursos como body
     for( let idx = 0 ; idx < recursos.length ; idx++ ){
          if( recursos[idx].obj !== ''){
               bundle.entry.push (
                    await LoadBundle(
                         recursos[idx].resource,
                         recursos[idx].obj,
                         idPatient,
                         recursos[idx].identifier,
                         recursos[idx].makeResource,
                         recursos[idx].makeArgs,
                         recursos[idx].text,
                         recursos[idx].daily,
                         date
                    )
               );
          }
     };

     // bundle.entry.forEach( (obj,idx) => {
     //      console.log(`${idx} : `, obj, obj.resource.identifier[0].value );
     // });
     console.log(bundle);

     requestOptionsPost.body =  JSON.stringify( bundle )

     const config = addToken(requestOptionsPost) ;
     config.headers.append("Content-Type", "application/json") ;

     const respBundle = await fetch( `${urlServer}`, config ) ;
     const objBundle = await respBundle.json()

     // if( objBundle.entry ){
     //      objBundle.entry.forEach( (item,idx)=>{
     //           console.log(`${idx} : `, item.response.status,"=>",item.response.location );
     //      })
     // };

     let t2 = new Date();
     console.log( "Tiempo : ", t2-t1,"ms" );
     console.log(objBundle);
     console.log( "Bundle_Response",
          objBundle.issue
               ?  JSON.stringify( objBundle.issue[0].details.text )
               :  objBundle.entry.map( (entry,idx) => [entry.response?.resource?.issue, entry.response.status ,idx]).filter( array => array[1] !== '200' )
     );
     console.log("Objeto enviado:", objBundle.issue ? bundle: 'OK' );
     return([
          objBundle.issue ?  JSON.stringify(objBundle.issue[0].details.text)  : 'OK',
          bundle
     ]);

};
