import React,{useEffect, useState} from 'react';
// Material UI
import { Box, Grid, Button } from '@mui/material';
// Estilos
import { Stl } from '../../Stl';
import { MediaQuery } from '../../MediaQuery' ;
import { Title }  from '../../../uci/components/elements/Title';

// Componentes
import { TextInput } from './helpers/TextInput';
import { Alarmas } from './helpers/Alarmas' ;
import { Ingresos } from './helpers/Ingresos' ;
import { BalanceHidrico } from './helpers/BalanceHidrico' ;
import { ExamenFisico } from './helpers/ExamenFisico' ;
import { ProgramacionEnfermeria } from './helpers/ProgramacionEnfermeria' ;
import { LoadButton } from './helpers/LoadButton' ;

// descripciones
import { descripciones } from './helpers/descripciones' ;
// claculo de horas
import { calcHours }  from './helpers/calcHours';

// let data = JSON.parse( localStorage.getItem('Data') );

export const Plano = ({change}) => {

     const classes = Stl();
     const {_xl,_lg} = MediaQuery();

     const [jornada, setJornada] = useState('DA');

     const [data, setDta] = useState(
          JSON.parse( localStorage.getItem('Data') )
     ) ;

     useEffect( ()=>{
          // dta = JSON.parse( localStorage.getItem('Data') );
          setDta(JSON.parse( localStorage.getItem('Data') ))
     },[jornada,change]);

     return(
          <Box
               pt={_xl ? 3 : 2}
               px={_xl ? 3 : 2}
               mt={_xl ? 3 : 2}
               // className={[classes.bg_white, classes.scrollContainer].join(' ')}
               className={[classes.bg_white].join(' ')}
          >

               {
                    ['DA', 'NOCHE'].map( (item, idx) =>
                         <Button
                              variant="outlined"
                              mx={1}
                              style={{fontFamily: 'Montserrat !important',
                                   fontSize:_xl ? '25px' : _lg ? '20px': '14px',
                                   fontWeight: 'bold',
                              }}
                              key={idx}
                              onClick={()=>setJornada(item)}
                         >
                              {item}
                         </Button>
                    )
               }
               <Alarmas jornada={jornada} dataOut={data} />

               <Ingresos dataOut={data} jornada={'DA'} />

               <BalanceHidrico dataOut={data}/>

               <ExamenFisico dataOut={data} />

               <Grid
                    container
                    direction="row"
                    justifyContent="space-around"
                    alignItems="flex-start"
                    className={classes.bg_white}
                    spacing={1}
               >
                    <Grid xs={12} item container mt={3}>
                         <Title color='blue'>
                              Tiempo desde la configuracin del dispositivo
                         </Title>
                    </Grid>
                    <Grid xs={12} item container mb={3}>
                         {
                              [
                                   'TOT','TQT','CVC','CUP','MPT','CF'
                                   ,'CHD','CSG','SNG','SNE','SNY','LA'
                                   ,'VVP','CPD','S.Foley','Drenajes'
                              ].map( (label,idx) =>
                                   <TextInput field={label} key={idx}  dataOut={data}  width={'15'} disable placeHol={`${calcHours(label,data)}h`} help={descripciones[label] ? descripciones[label] : false} />
                              )

                         }
                    </Grid>

                    <Grid xs={6} item container my={3}>
                         <TextInput field={'NOMBRE: Enfermero Turno Largo'}  dataOut={data}  fullwidth />
                    </Grid>

                    <Grid xs={6} item container my={3}>
                         <TextInput field={'NOMBRE: Enfermero Turno Nocturno'} dataOut={data}  fullwidth />
                    </Grid>
               </Grid>

               <ProgramacionEnfermeria dataOut={data} />

               <LoadButton />

          </Box>
     );
};
