import React,{useState} from 'react' ;
// mui
import { Box} from '@mui/material';
// helpers
import { join} from '../../../join' ;
import { MediaQuery } from '../../../MediaQuery'
// Descripciones
import { descripciones } from './descripciones';
import { Hint } from './Hint';

export const TablaAlarmas = ({data, jornada}) => {

     const { _xl, _lg} = MediaQuery();

     const ArrayInputs = ({num, renglon, field, size='6'}) =>{

          const [dta,setDta] = useState({
               [`${field}`] : ['+*+','+*+','+*+','+*+','+*+','+*+'].map( (_,idx) =>
                    renglon?.[`${field}`][idx] ? renglon[`${field}`][idx] : '+*+'
               )
          });

          const handleChange = ( event, idx, set) => {

               let value = event.target.value;//.replace(/[^0-9%:.-]/g, '');

               dta[`${field}`][idx] =  value === '' ? '+*+' : value ;

               setDta(
                    {...dta}
               );

          };

          return(
               [...new Array(num)].map( (item, idx) => {
                    return(
                         <input
                              key={idx}
                              id={`${field}_${idx}`}
                              style={{textAlign:'center'}}
                              value={ dta[`${field}`]
                                        ?  dta[`${field}`][idx]
                                             ? field !== 'Hora'
                                                  ? dta[`${field}`][idx].replace( /[^0-9%.-]/g,'')
                                                  : dta[`${field}`][idx].replace( /[^0-9:AaMm.Pp ]/g,'')
                                             : ' '
                                        :  ' '
                              }
                              type='text'
                              size={size}
                              tabIndex={ `${field}_${idx}` }
                              className={join(['textField','py','px','celdText'])}
                              onChange={ (event) => handleChange(event,idx) }
                         />
                    )
               })
          );
     };

     const fieldsAlarmas = [
          'Hora','TC','FC','Cdigo','P/A','PAM','PVC','PAP','PCP','ngulo de Cabecera','EVA/S.A.S',
          'FR','Cd./Modo','FIO2','IPAP/PS','EPAP/PEEP','PC','VOL.CTE','VOL.MIN','SAT','CUFF/CMS',
          'DU Parcial','DU Total','Deposiciones','HGT','Insulina','Residuo Gstrico','Evaluacin contenciones',
     ];

     const renglonDatos = fieldsAlarmas.map( (elm,idx) => {

          const obj={}

          if( data?.['alarmas'] && data?.['alarmas']?.[`${jornada}`]  ){

               obj[elm] = data['alarmas'][`${jornada}`].find( item => elm === Object.keys(item)[0] )
                    ? [...data['alarmas'][`${jornada}`].find( item => elm === Object.keys(item)[0] )[elm] ]
                    : ['+*+','+*+','+*+','+*+','+*+','+*+'] ;

          }else{
               obj[elm] = ['+*+','+*+','+*+','+*+','+*+','+*+'];
          };

          return(
               obj
          );
     })

     const handleBlur = ( e, idx, item ) =>{

          let idArray = e.target.id.split('_')[1] ;

          let value = e.target.value.replace(/[^a-zA-Z 0-9%+:,.]/g, '');

          renglonDatos[idx][item][idArray] =  value === '' ? '+*+' : value ;

          data  = JSON.parse( localStorage.getItem('Data') ) ;
          data['alarmas'][`${jornada}`] = renglonDatos ;

          localStorage.setItem('Data',JSON.stringify(data, null, 2)) ;
     };

     return(
          <Box>
               {
                    fieldsAlarmas.map( (item, idx1) =>{
                         return(
                              <div
                                   key={idx1}
                                   onBlur={ (e)=>handleBlur(e, idx1, item) }
                                   style={{paddingBottom:'0.1rem'}}
                              >
                                   <div
                                        style={{width:_xl? '14rem':_lg?'14rem':'10rem', display:'inline-block',position: 'relative'}}
                                        className={join(['title3', 'text_color_title', 'text_bold','font_monserrat'])}
                                   >
                                        <div style={{display:'flex',alignItems: 'center'}}>
                                             { item }
                                             <Hint help={descripciones[item] ? descripciones[item] : false}/>
                                        </div>
                                   </div>

                                   <ArrayInputs
                                        num={6}
                                        field={ item }
                                        renglon={ renglonDatos[idx1] }
                                        size={ _xl ?'18' :_lg ? '19': '14' }
                                   />

                              </div>
                         )
                    })
               }
          </Box>
     );
};
