import React,{useState} from 'react' ;
// helpers
import { join} from '../../../join' ;
// descripciones
import { descripciones } from './descripciones';
import { Hint } from './Hint';

export const pullPush = ( field, data ) => {

     const pull = ( array ) => {
          const key =  array.find( (elm) =>
               (localStorage.getItem(`${elm}`)
               ? JSON.parse(localStorage.getItem(`${elm}`))
               : false) !== false
          )

          let obj = {} ;
          if( key !== undefined ){
               obj =  JSON.parse(localStorage.getItem(`${key}`))  ;
               localStorage.removeItem(`${key}`);
          }

          return(
               obj
          )

     };

     const push = (field, data, idx) =>{
          const obj= {field:`${field}`,value:data, idx:idx }
          localStorage.setItem(`${field}`,JSON.stringify(obj, null, 2)) ;
     };

     return({pull, push}) ;

};

export const fieldCellsText = ( {field, cells, data, typ='text', tabIndex} ) => {

     const Text = ({ data, field, idx, typ}) =>{

          const {push} = pullPush() ;

          const [dta, setDta] = useState(
               { val: data }
          );


          const handleChange =( event )=>{
               setDta(
                    {val : event.target.value  }
               );

          };

          const handleBlur = ( event,idx ) =>{
               push(field, event.target.value, idx);

               setDta(
                    {val : event.target.value }
               );
          };

          return(
               <input
                    value={ dta.val }
                    type={typ}
                    style={{width:'100%',textAlign:'center',boxSizing:'border-box'}}
                    tabIndex={ `${tabIndex}` }
                    className={join(['textField','py','celdText'])}
                    onChange={ (event)=>handleChange(event) }
                    onBlur = { (event)=>handleBlur(event,idx) }
               />
          );
     };

     return(
          [
               <div
                    className={join( [ 'textField', 'bg_white', 'text_color_title','text_bold' ] )}
                    style={{display:'flex', alignItems:'center',position:'relative'}}
               >
                    {field}
                    <Hint help={ descripciones[field] ? descripciones[field] : false }/>
               </div>
               ,
               ...[ ...new Array(cells) ].map( (_,idx)=>
                    <Text
                         field ={ field }
                         data={ data[field] ? data[field][idx] ? data[field][idx] : '' : ''}
                         idx={idx}
                         typ={typ}
                    />
               )
          ]
     );
};
