import {useEffect, useState} from 'react' ;
// Material UI
import { Box, Grid } from '@mui/material';
import Divider from '@mui/material/Divider';
// Estilos
import { Stl } from '../../Stl';
import { MediaQuery } from '../../MediaQuery' ;
// Componentes
import { TextInput } from './helpers/TextInput';
import { CheckBoxTextInput } from './helpers/CheckBoxTextInput';
import { RadioButton } from './helpers/RadioButton';
import { MltLineObj } from './helpers/MltLineObj';
import { MltObjText } from './helpers/MltObjText';
// helpers
import { join} from '../../join' ;
// descripciones
import { descripciones } from './helpers/descripciones';

// let data = JSON.parse( localStorage.getItem('Data') );

export const IndicacionesMedicas = ({change}) => {

     const classes = Stl();
     const {_xl,_lg} = MediaQuery();


     const [data, setDta] = useState(
          JSON.parse( localStorage.getItem('Data') )
     ) ;

     useEffect( ()=>{
          // dta = JSON.parse( localStorage.getItem('Data') );
          setDta(JSON.parse( localStorage.getItem('Data') ))
     },[change]);

     return(
          <Box
               pt={_xl ? 3 : 2}
               px={_xl ? 3 : 2}
               mt={_xl ? 3 : 1}
               pb={3}
               // className={[classes.bg_white, classes.scrollContainer].join(' ')}
               className={[classes.bg_white].join(' ')}
          >
               <Grid
                    container
                    direction="row"
                    justifyContent="space-around"
                    alignItems="flex-start"
                    className={classes.bg_white}
                    spacing={1}
               >
                    <Grid xs={12} item container my={3}>
                         <div
                              className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
                         >
                              <Divider textAlign="center">
                                   INDICACIONES MEDICAS
                              </Divider>
                         </div>
                    </Grid>

                    <Grid xs={12} item container justifyContent="center">
                         <MltObjText field='Riesgo de UPP' fields={['Bajo','Medio', 'Alto' ]} dataOut={data}/>
                         <MltObjText field='Riesgo de Cada' fields={['Contencin','Barandas', 'freno' ]} dataOut={data}/>
                    </Grid>

                    <Grid xs={6} item>
                         <RadioButton dataOut={data} field='Riesgo Enf. Tromboemblica' fields={['Leve','Moderado','Alto','Muy Alto']}/>
                    </Grid>

                    <Grid xs={6} item>
                         <RadioButton dataOut={data} field='Aislamiento' fields={['Contacto','Goticas','Respiratorio','Protector','NA']}/>
                    </Grid>

                    <Grid xs={6} item>
                         <TextInput field={'Reposo'}  width='35' dataOut={data} />
                    </Grid>

                    <Grid xs={6} item>
                         <TextInput field={'Rgimen'}  width='20' dataOut={data} />
                    </Grid>

                    <Grid xs={4} item>
                         <CheckBoxTextInput field1='Profilxis Mecnica (TEV)' field2='Especifique' opc={['No','Si']} dataOut={data} help={descripciones['TEV']}/>
                    </Grid>

                    <Grid xs={4} item>
                         <RadioButton dataOut={data} field='KTR' fields={['Respiratorio','Motora']} help={descripciones['KTR']}/>
                    </Grid>

                    <Grid xs={4} item>
                         <RadioButton dataOut={data} field='Destete' fields={['Si','No']} help={descripciones['Destete']}/>
                    </Grid>

                    <Grid xs={12} item container my={_xl?3:_lg?3:1}>
                         <div
                              className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
                         >
                              <Divider textAlign="center">
                                   Medicacin
                              </Divider>
                         </div>
                         {
                         // tabla dinamica de indicaciones medicas
                         }
                         <MltLineObj  dataOut={data} field={'indicaciones'} fields={['Medicamentos','Va','Dosis','Frecuencia']} size={[4,3,3,2]}/>
                    </Grid>

                    <Grid item container directio={'row'}
                         justifyContent="space-around"
                         alignItems="start"
                    >
                         <Grid xs={3} item>
                              <RadioButton dataOut={data} field='Profilaxis Farmacologica' fields={['No','Si']} help={descripciones['Profilaxis']}/>
                         </Grid>

                         <Grid xs={3} item>
                              <RadioButton dataOut={data} field='Contenciones' fields={['No','Si']} help={descripciones['Contenciones']}/>
                         </Grid>

                         <Grid xs={3} item pt={2}>
                              <TextInput field={'Nombre Doctor'}  dataOut={data} fullwidth />
                         </Grid>

                         <Grid xs={3} item pt={2}>
                              <TextInput field={'Cdigo'}  dataOut={data} fullwidth />
                         </Grid>
                    </Grid>

                    <Grid xs={12} item container my={_xl?3:_lg?3:1}>

                         <div
                              className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
                         >
                              <Divider textAlign="center">
                                   Mantencin de Dispositivos
                              </Divider>
                         </div>
                    </Grid>

                    <Grid xs={12} item container   justifyContent="space-around"  >
                         {
                              // ['CUP','TOT/TQT','CHD','CVC','LA','SNE']
                         }
                         {
                              [
                                   'TOT','TQT','CVC','CUP','MPT','CF'
                                   ,'CHD','CSG','SNG','SNE','SNY','LA'
                                   ,'VVP','CPD','S.Foley','Drenajes'
                              ].map( (label,idx) =>
                                   <RadioButton key={idx} dataOut={data} field={label} fields={['No','Si']} help={descripciones[label]}/>
                              )
                         }

                    </Grid>
               </Grid>
          </Box>
     );
};
