

import {ifExits} from './ifExits'; // idExits( 'prop', obj )

export const loadObservationValueString = ( data, id, identifier, code, valueItem ) => {

     return(
          {
              resourceType: "Observation",
              identifier: [
                 {
                     use: "official",
                     value: `${identifier}`
                 }
              ],
              status: "registered",
              code: {...code},
              valueString :ifExits( valueItem , data ),
              subject: {
                  reference: `Patient/${id}`
              }
         }
     );
};
