import React,{useState, useEffect} from 'react';
// Material UI
// import { Box, Grid } from '@mui/material';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
// Estilos
import { Stl } from './Stl';

// fomulario
import { Plano } from './components/formulario/Plano';
import { PersonalInformation } from './components/formulario/PersonalInformation';
import { IndicacionesMedicas } from './components/formulario/IndicacionesMedicas';

// Componentes
import { HistoriaClinica } from '../uci/components/monitorPaciente/HistoriaClinica';
import { InformacionClinica } from '../uci/components/monitorPaciente/InformacionClinica';
import { TabInfoClinica } from '../uci/components/monitorPaciente/TabInfoClinica';
import { Balance } from '../uci/components/monitorPaciente/Balance';
import { Examenes } from '../uci/components/monitorPaciente/Examenes';
import { Alertas } from '../uci/components/monitorPaciente/Alertas';

// import { MediaQuery }  from './MediaQuery';

// store
import { useDispatch } from 'react-redux'
import { assignId } from '../../redux/patientSlice'
import { assignDay } from '../../redux/dayNowSlice' ;

const Formulario = () => {

     const dispatch = useDispatch();

     const classes = Stl() ;

     const d = new Date();
     const dayNow = [d.getDate(),d.getMonth()+1 ,d.getFullYear()];

     const id = localStorage.getItem('idPatient') ? JSON.parse(localStorage.getItem('idPatient')) : ''

     const [change, setChange] = useState(null);
     const [ stateData, setStateData ] = useState( !localStorage.getItem('Data') );


     useEffect( ()=>{
          dispatch( assignDay(dayNow) );
          dispatch( assignId(id) );

          const clock = setInterval(() => {
               setStateData(!localStorage.getItem('Data'));
          }, 1000);

          return () => clearInterval(clock);

     });

     document.getElementById("root").style.backgroundColor='#d1e1e5' ;



     return (
          <Box>
               {
                    stateData
                    ?
                         <h3>
                              Cargando elementos...
                         </h3>
                    :
                         <div id="interface" style={{ width: '100%' }} >

                              <Box
                                   className={ classes.bg_blue }
                              >
                                   <p className={[classes.text_bold,  classes.text_white,  classes.text, classes.my_0, classes.title1].join(' ')}>
                                        Control Clnico e indicaciones
                                   </p>
                              </Box>

                              <Grid
                                   container
                                   direction="row"
                                   justifyContent="flex-start"
                              >
                                   {/* Columna izquierda */}
                                   <Grid item xs={5} >

                                        <Box pr={1} pt={2} >

                                             <HistoriaClinica data={[id]} stl={classes}  />

                                             <InformacionClinica data={[id]} stl={classes} />

                                             <TabInfoClinica data={[ id, dayNow ]} stl={classes}/>

                                             <Balance data={[ id, dayNow ]} stl={classes}/>

                                             <Box mt={1}>
                                                  <Grid container spacing={2} >

                                                       <Grid item xs={6} >
                                                            <Examenes data={[ id, dayNow ]} stl={classes} />
                                                       </Grid>

                                                       <Grid item xs={6} >
                                                            <Alertas data={[ id, dayNow ]} stl={classes}/>
                                                       </Grid>

                                                  </Grid>
                                             </Box>
                                        </Box>

                                   </Grid>

                                   {/* Columna Derecha */}


                                   <Grid item xs={7}>
                                        <PersonalInformation  change={change} set={setChange}/>
                                   </Grid>

                                   <Grid item xs={12}>
                                        <IndicacionesMedicas change={change} />
                                        <Plano change={change} />
                                   </Grid>

                              </Grid>
                         </div>
               }
          </Box>
     );
};

export default Formulario;
