import {ifExits} from './ifExits' ;

export const addDate = (data) => {
     if( ifExits('Ao', data) === '--' ){
          const d = new Date();
          return([
               `${d.getFullYear()}`.replace(/ +/g,''),
               d.getMonth()+1 < 10 ?
                    `0${d.getMonth()+1}`.replace(/ +/g,'')
                    : `${d.getMonth()+1}`.replace(/ +/g,''),
               d.getDate() < 10 ?
                    `0${d.getDate()}`.replace(/ +/g,'')
                    : `${d.getDate()}`.replace(/ +/g,'')
          ])
     };
     return ([
          data['Ao'],
          data['Mes'] < 10 
               ? `0${ String( data['Mes'] ) }`.replace(/ +/g,'')
               : String( data['Mes'] ).replace(/ +/g,''),
          data['Da'] < 10 
               ? `0${ String( data['Da'] ) }`.replace(/ +/g,'')
               : String( data['Da'] ).replace(/ +/g,''),
     ])
};
