import React from 'react' ;
// mui
import { Box, Grid } from '@mui/material';
import Divider from '@mui/material/Divider';
// helpers
import { join } from '../../../join' ;
import { Stl } from '../../../Stl' ;
// Componentes
import { ArrayGrid } from './ArrayGrid' ;
import { fieldCellsText, pullPush } from './fieldCellsText' ;
import { RadioButton } from './RadioButton';
import { TextInput }  from './TextInput' ;

export const ProgramacionEnfermeria = ({dataOut}) =>{

     const {_xl, _lg } = Stl();

     const fieldsIzq = [
          'Aseo y Confort','Aseo Matinal','Bao en Cama','Lavado de Pelo','Aseo Genital','Aseo Cavidades','Aseo Ocular',
          'Prevencin UPP','Colchn ant-esc.','Movilizacin','Riesgo Medio(4h)','Riesgo alto(2h)','Medidas Aisl.','Bao Clorex',
          'Aseo Bucal'
     ];

     const fieldsDer = [
          'Procedimientos','Curacin CVC','Cms0','Sitio de insercin','Puntos de fijacin','UPP','Ubicacin','Grado',
          'Instalacin Dispositivos','VVP/LA/SF','Cms1','Cms2','Cms3'
     ];

     const { pull } = pullPush() ;


     const handleBlur = ( key ) =>{

          dataOut = JSON.parse( localStorage.getItem('Data') ) ;

          const {field, value} = pull( key === 'izq' ? fieldsIzq : fieldsDer  ) ;


          dataOut[`ProgramacionEnfer`][field] = dataOut[`ProgramacionEnfer`][field]
                                             ? [...dataOut[`ProgramacionEnfer`][field]]
                                             : [] ;

          dataOut[`ProgramacionEnfer`][field] = [value] ;


          localStorage.setItem('Data',JSON.stringify(dataOut, null, 2))
     };

     const handleBlurRadio = ( event, col, radio=true ) =>{
          event.preventDefault();

          dataOut = JSON.parse( localStorage.getItem('Data') ) ;

          dataOut[`ProgramacionEnfer`][`${col}`] = dataOut[`ProgramacionEnfer`][`${col}`]
                                                       ? dataOut[`ProgramacionEnfer`][`${col}`]
                                                       : {} ;

          radio
               ? Object.assign(dataOut[`ProgramacionEnfer`][`${col}`], {[ event.target.name]:event.target.value} )
               : col === 'izq'
                    ? dataOut[`ProgramacionEnfer`][`${col}`]['NOMBRE: Enfermero Turno Diurno'] = event.target.value
                    : dataOut[`ProgramacionEnfer`][`${col}`]['NOMBRE: Enfermero Turno Nocturno'] = event.target.value  ;

          localStorage.setItem('Data',JSON.stringify(dataOut, null, 2))
     };

     return(
          <Box>
               <div
                    className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
               >
                    <Divider textAlign="center">
                         Programacin de enfermera
                    </Divider>
               </div>
               <p
                     className={join(['text_color_title','font_monserrat','fullwidth','my_2'])}
               >
                    Hora(am/pm) - tiempo evento en minutos(m) : 07:00am - 10m
               </p>
               <Grid
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="flex-start"
                    spacing={ _lg ? 3 : 1}
               >
                    <Grid xs={6} item  onBlur={()=> handleBlur('izq')} >

                              {
                                   fieldsIzq.map( (elm,idx)=>
                                        <ArrayGrid
                                             key={idx}
                                             arrayObj={ fieldCellsText ({field:elm, cells:1, data:dataOut.ProgramacionEnfer, tabIndex:`Izq_${idx}` }) }
                                             dimension={[5,7]} col={12}
                                        />
                                   )
                              }
                    </Grid>

                    <Grid xs={6} item onBlur={ ()=> handleBlur('der')}  >
                              {
                                   fieldsDer.map( (elm,idx)=>
                                        <ArrayGrid
                                             key={idx}
                                             arrayObj={ fieldCellsText ({field:elm, cells:1, data:dataOut.ProgramacionEnfer, tabIndex:`Der_${idx}` }) }
                                             dimension={[5,7]} col={12}
                                        />
                                   )
                              }
                    </Grid>
                    {
                         ['izq','der'].map( (col,idx1) =>
                              <Grid xs={6} item container key={idx1}>
                                   <Grid xs={12} item >
                                        <h1
                                             style={{  fontFamily: 'Montserrat',
                                                       color:'#0d47a1',
                                                       fontSize: _xl ? '25px' : _lg ? '22px': '15px',
                                                       fontWeight: 'bold',
                                                       lineHeight: '100%' }}
                                        >
                                             {`Contencin Fsica ${col === 'izq' ? '8-20' : '20-8'}  (>5 pts)`}
                                        </h1>
                                   </Grid>
                                   <Grid xs={12} item
                                        container
                                        justifyContent="space-evenly"
                                        direction="row"
                                        onChange={(event) => handleBlurRadio(event, col, true)}
                                   >
                                        {
                                             ['BD','BI','PD','PI','Trax','NA'].map( (label,idx2) =>
                                                  <Grid xs={4} item key={idx2}>
                                                       <RadioButton
                                                            dataOut={  col === 'izq' ? dataOut.ProgramacionEnfer.izq : dataOut.ProgramacionEnfer.der }
                                                            field={ label }
                                                            fields={['Si','No']}
                                                            width
                                                            save={false}
                                                       />
                                                  </Grid>
                                             )
                                        }
                                   </Grid>
                                   <Grid xs={12} item
                                        onBlur={(event) => handleBlurRadio(event, col, false)}
                                   >
                                        <TextInput
                                             field={`NOMBRE: Enfermero Turno ${col==='izq'?'Diurno':'Nocturno'}`}
                                             dataOut={ col === 'izq' ? dataOut.ProgramacionEnfer.izq : dataOut.ProgramacionEnfer.der  }
                                             fullwidth
                                             save={false}
                                        />
                                   </Grid>
                              </Grid>
                         )
                    }
               </Grid>
          </Box>
     );
};
