

import {ifExits} from './ifExits' ;  // idExits( 'prop', obj )
import {addDate} from './addDate' ;

export const loadAccount = ( data, id ) => {

     return(
          {
               resourceType : "Account",
               identifier : [
                    {
                         use: 'official',
                         value: `Account`,
                         period:{
                              start: `${addDate(data)[0]}-${addDate(data)[1]}-${addDate(data)[2]}`,
                              end: ifExits('Fecha', data )
                         }
                    }
               ],
               status : "active",
               name : ifExits( 'RUT', data ),
               subject : [
                    {
                         reference: `Patient/${id}`
                    }
               ],
               description: ifExits('UCI', data )
          }
     );

} ;
