

import {ifExits} from './ifExits'; // idExits( 'prop', obj )

export const loadBasic = ( data, id, identifier, arrayElements, text ) => {

     const coding = arrayElements
                         .filter( item => ifExits( item, data ) !== ' ' )
                         .map( (item, idx) => {
                              return(
                                   {
                                        display: String( ifExits( item, data ) ).length === 0
                                                  ? String( ifExits( item, data ) ) + ' '
                                                  : String( ifExits( item, data ) ).replace(/  +/,''), // Bora espacios finales
                                        version: String( ifExits( `${item}_version`, data ) )
                                   }
                              )
                         });

     return(
          {
              resourceType: "Basic",
              identifier: [
                    {
                         use: "official",
                         value: `${identifier}`
                    }
              ],
              code:{
                  coding,
                  text: `${text}`
              },
              subject: {
                   reference: `Patient/${id}`
              }
          }
     );
};
