import React from 'react' ;
// mui
import { Box, Grid} from '@mui/material';
import Divider from '@mui/material/Divider';
// helpers
import {join} from '../../../join' ;
// Componentes
import { ArrayGrid } from './ArrayGrid' ;
import { fieldCellsText, pullPush } from './fieldCellsText' ;
import { TextInput } from './TextInput';

export const ExamenFisico = ( {dataOut} ) =>{

     const head = () => {
          return(
               ['Parmetro','Da','Noche'].map( (elm,idx) =>
                    <span
                         key={idx}
                         className={join( ['textField', 'bg_white', 'text_color_title','text_bold'] )}
                    >
                         {elm}
                    </span>
               )
          );
     };

     const fields = [
          'Estado general','Conciencia','Act. Motor','Comportamiento','Piel','Cabeza','Cuello','Conjuntiva','Escleras','Pupilas',
          'F. Nasales','Labios','Lengua','Dentadura','Yugurales','Traz','Abdomen','Regin Lumbar','Sacro','Genitales','Regin Perianal',
          'ESD','ESI','EID','EII','Protecci zonas de apoyo','N TOT/cm','VM'
     ];


     const data = {
          value: dataOut?.[`examen fisico`]
                         ? dataOut[`examen fisico`]
                              ? dataOut[`examen fisico`]
                              : ''
                         :'',
          // Da: dataOut?.[`examen fisico`]
          //                ? dataOut[`examen fisico`][`Da`]
          //                     ? dataOut[`examen fisico`][`Da`]
          //                     : ''
          //                :'',
          // Noche: dataOut?.[`examen fisico`]
          //                ? dataOut[`examen fisico`][`Noche`]
          //                     ? dataOut[`examen fisico`][`Noche`]
          //                     : ''
          //                :''
     } ;

     const { pull } = pullPush();

     const handleBlur = () => {

          dataOut = JSON.parse( localStorage.getItem('Data') ) ;
          
          const {field, value, idx} = pull( fields ) ;

          dataOut[`examen fisico`] = dataOut[`examen fisico`] ? {...dataOut[`examen fisico`]} : {} ;
          dataOut[`examen fisico`] = dataOut[`examen fisico`] ? {...dataOut[`examen fisico`]}: {} ;
          dataOut[`examen fisico`][field] = dataOut[`examen fisico`][field] ? [...dataOut[`examen fisico`][field]] : [];

          dataOut[`examen fisico`][field][idx] = value ;

          localStorage.setItem('Data',JSON.stringify(dataOut, null, 2))
          // setdata({
          //      Dia: {...dataOut['examen fisico'].Dia},
          //      Noche: {...dataOut['examen fisico'].Noche},
          // });

     };

     return(
          <Box>
               <div
                    className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
               >
                    <Divider textAlign="center">
                         {`Examen Fsico`}
                    </Divider>
               </div>

               <Grid
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="flex-start"
                    spacing={1}
               >
                    <Grid xs={12} item onBlur={ () => handleBlur() }>
                              <ArrayGrid  arrayObj={ head() } dimension={[2,2,2]} col={6} />
                              {
                                   fields.map( (elm,idx) =>
                                        <ArrayGrid
                                             key={idx}
                                             arrayObj={ fieldCellsText ({field:elm, cells:2, data:data.value }) }
                                             dimension={[2,2,2]} col={6}
                                        />
                                   )
                              }
                    </Grid>
                    <div
                         className={join(['title1','text_color_title','text_bold','text_center','font_monserrat','fullwidth','my_2'])}
                         align='center'
                    >
                         Observaciones de Evolucin
                    </div>
                    {
                         ['Da','Noche'].map( (jornada, idx) =>
                              <Grid xs={6} item container direction={'row'} key={idx}>
                                   {
                                        ['Cardiovascular','Respiratorio','Renal',
                                             'Neurologco','Alimentacin','Eliminacin',
                                             'Post-Quirrgico'
                                        ].map( (label, idx_label) =>
                                             <Grid xs={ idx_label === 0 ? 12 : 6} item key={idx_label}>
                                                  <TextInput field={`${label}-${jornada}`} fullwidth={true} dataOut={dataOut} mltLine={true} maxRow={3}/>
                                             </Grid>
                                         )
                                   }
                              </Grid>
                         )
                    }
               </Grid>
          </Box>
     );
};
