

import {ifExits} from './ifExits'; // idExits( 'prop', obj )
import {addDate}from './addDate' ;

export const loadCommunication = ( data, id, identifier, arrayElements ) => {

     const payload = arrayElements.map( (item, idx) => {
          return(
               {
                  contentString: ifExits( item, data )
               }
          )
     });

     return(
          {
               resourceType : "Communication",
               identifier:[
                    {
                         use:"usual",
                         value: identifier
                    }
               ],
               status: "completed",
               received:`${addDate(data)[0]}-${addDate(data)[1]}-${addDate(data)[2]}`,
               subject:{
                    reference: `Patient/${id}`
               },
               payload
          }
     );
};
