export const LoadBundle = async ( resource, data, idPatient, identifier, func, args, text, daily, date) => {

     let request = {} ;

     let today = `${date}T00:00:00Z` ;
     // let tomorrow = date.split('-').reduce( (acc,_,__,arr)=> acc=`${arr[0]}-${arr[1]}-${parseInt(arr[2])+1}T00:00:00Z`) ;

     const url = daily
                    ? `${resource}?subject=${idPatient}&identifier=${identifier}&identifier=${today}`
                    : `${resource}?subject=${idPatient}&identifier=${identifier}`   ;

     // if( await CheckResourceExist( url ) ){
          request = {
               method: 'PUT',
               url: url
          };
     // }else{
     //      request = {
     //           method: 'POST',
     //           url:`${resource}`
     //      };
     // };

     return (
          {
               resource: func( data, idPatient, `${identifier}`, ...args, text ),
               request
          }
     ) ;

};
